import React from "react";
import { motion } from "framer-motion";
import "./about.css"; // CSS dosyan dahil etmeyi unutma

function About(){
  return (
    <div className="about">
      <motion.div 
        className="about-container"
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, ease: "easeOut" }}
      >
        <h1 className="about-title">Hakkmda</h1>
        <p className="about-text">
          Merhaba! Ben Burak Karahan, yazlm gelitirme ve web tasarm ile ilgileniyorum. 
          zellikle backend teknolojileri, ve kullanc deneyimi tasarm konusunda 
          kendimi gelitiriyorum. Projelerimde modern teknolojiler kullanarak 
          estetik ve ilevsel zmler retmeyi hedefliyorum.
        </p>
        <div className="about-details">
          <p>&nbsp; &nbsp;&nbsp;&nbsp; Lokasyon: &nbsp; Elaz</p>
          <p> &nbsp; Eitim: &nbsp; Frat niversitesi / Yazlm Mhendislii</p>
          <p> &nbsp; lgi Alanlar: &nbsp; Java Spring Boot*, &nbsp; React, &nbsp; Next.js, &nbsp; UI/UX</p>
        </div>
      </motion.div>
    </div>
  );
};

export default About;
